@keyframes opacity-inc {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.backdrop {
  position: absolute;
  right: 0;
  height: 200vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 90;
  transition: all 500ms ease-in-out;
  opacity: 0;
  visibility: hidden;

  &.backdrop-active {
    animation: opacity-inc 500ms;
    opacity: 1;
    visibility: visible;
  }
}
