@import '../../../media.scss';
@mixin underline {
    &::after {
        content: '';
        position: absolute;
        bottom: -9px;
        left: 0;
        width: 100%;
        height: 3px;
        background-color: $color-white;
    }
}

.navbar {
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.9);
    color: $text-color-default;
    width: 100%;
    font-size: $font-md;
    display: flex;
    justify-content: space-between;
    padding: 6px 12px;
    z-index: 99 !important;

    .navbar-nav {
        margin-right: 2vw !important;
        display: flex;
        justify-content: space-evenly;
        width: 70%;
        height: 100%;
        transition: width 0.2s ease;

        @include mediaQuery($screen-sm) {
            width: 60%;
        }

        @include mediaQuery($screen-md) {
            width: 50%;
        }

        @include mediaQuery($screen-lg) {
            width: 40%;
        }
    }

    .navbar_nav-brand {
        width: 15%;

        font-size: 18px;
        a {
            color: $text-color-default;
        }
    }

    .navbar_nav-link {
        color: $text-color-default !important;
        display: flex;
        justify-content: center;
        height: 100%;
        width: 25%;
        position: relative;

        &:hover {
            @include underline;
        }

        &.active {
            @include underline;
        }
    }

    .navbar-brand {
        color: $text-color-default !important;
        font-size: $font-md;
    }

    a {
        text-decoration: none;
        cursor: pointer;
    }
}
