//media queries
$mobile-sm: 320px;
$mobile-md: 375px;
$mobile-lg: 576px;

$tablet-sm: 768px;
$tablet-lg: 1024px;

$screen-sm: 1200px;
$screen-md: 1366px;
$screen-md-custom: 1600px;
$screen-lg: 1920px;

//media queries

@mixin mediaQuery($screen) {
    @media (min-width: $screen) {
        @content;
    }
}

@mixin customMediaQuery($screen) {
    @media (min-width: $screen+'px') {
        @content;
    }
}

//font-sizes
$font_xs: 8px;
$font_sm: 16px;
$font_md: 18px;
$font_md-custom-s: 24px;
$font_md-custom: 28px;
$font_lg: 32px;
$font_xl: 48px;
$font_xl-custom: 52px;
$font_xxl: 64px;
$font_xxxl: 95px;
$font_heading_sm: 54px;
$font_heading_lg: 124px;

//colors
$color-black: #000000;
$color-white: #ffffff;
$text-color-default: #f8f9fa;
$highlight-color-blue: #016fb9;
$background-color-blue: #22446b;

//values

$initialLineHeight: 26px;
$finalFineHeight: 32px;
$horizontal_sm: -20px;
$horizontal_lg: -30px;
$border_sm: 3px solid $color-white;

//gradient & shadow

$background_gradient: linear-gradient(
    to bottom,
    rgba(18, 31, 46, 0.92) 0%,
    $background-color-blue 30%
);

@mixin background_blue {
    background: $background-color-blue;
}

@mixin shadow {
    -webkit-box-shadow: inset 0px 0px 17px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: inset 0px 0px 17px 0px rgba(0, 0, 0, 0.75);
    box-shadow: inset 0px 0px 17px 0px rgba(0, 0, 0, 0.75);
}

@mixin background_shadow {
    background: $background_gradient;
    @include shadow;
}

@mixin heading_decorator {
    position: relative;

    &:before,
    &:after {
        content: '';
        opacity: 0.7;
        position: absolute;
        width: 40%;
        height: 50%;
        background-color: transparent;
    }

    &:before {
        top: -12px;
        left: $horizontal_sm;
        border-top: $border_sm;
        border-left: $border_sm;

        @include mediaQuery($mobile-sm) {
            left: calc(-0.014 * 100vw - 15.52px);
        }

        @include mediaQuery($tablet-lg) {
            left: $horizontal_lg;
        }
    }

    &:after {
        bottom: -12px;
        right: $horizontal_sm;
        border-bottom: $border_sm;
        border-right: $border_sm;

        @include mediaQuery($mobile-sm) {
            right: calc(-0.014 * 100vw - 15.52px);
        }

        @include mediaQuery($tablet-lg) {
            right: $horizontal_lg;
        }
    }
}
