@import '../../media.scss';
@mixin center {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.footer_container {
    max-width: 100vw;
    padding: 0;

    @include mediaQuery($tablet-sm) {
        @include background_shadow;
    }

    .footer {
        width: 100%;
        height: 100%;
        padding: 16px;
        padding-bottom: 32px;

        @include mediaQuery($tablet-sm) {
            margin-top: 40vh;
            padding: 32px 0;
        }

        .footer_time {
            @include center;

            .footer_hours_container {
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }

        .footer_contact {
            @include center;

            .footer_icons_container {
                width: 100%;
                display: flex;
                justify-content: space-evenly;
                align-items: center;
            }
        }
    }

    .footer_heading {
        @include heading_decorator;
    }

    .footer_text {
        position: relative;
        padding-bottom: 2vh;
        text-align: center;
        font-size: $font-sm;

        @include mediaQuery($mobile-sm) {
            font-size: calc(0.011 * 100vw + 12.48px);
        }

        @include mediaQuery($tablet-lg) {
            font-size: $font-md;
        }
    }
}
