@import '../../../media.scss';
$border_sm: 4px solid $color-white;
$border_lg: 6px solid $color-white;
$horizontal_sm: -20px;
$horizontal_lg: -30px;
$arrow_sm: 60px;
$arrow_lg: 80px;

@keyframes pulse {
    0% {
        transform: scale(0.95);
    }
    50% {
        transform: scale(1);
    }
    100% {
        transform: scale(0.95);
    }
}

@keyframes text-fade {
    from {
        opacity: 1;
        transform: scale(1);
    }
    25% {
        opacity: 0.5;
        transform: scale(0.6);
    }
    50% {
        opacity: 0;
        transform: scale(0);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes border {
    from {
        opacity: 0.7;
        width: 15%;
        height: 50%;
    }
    25% {
        opacity: 0.5;
    }

    50% {
        opacity: 0;
        width: calc(100% + 48px);
        height: calc(100% + 1px);
    }
    75% {
        opacity: 0.5;
    }
    to {
        opacity: 0.7;
        width: 15%;
        height: 50%;
    }
}

.heading_container {
    width: 90%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .heading_animated {
        margin: 0;
        color: white;
        font-size: $font_heading_sm;
        text-align: center;
        position: relative;

        @include mediaQuery($mobile-sm) {
            font-size: calc(0.099 * 100vw + 22.32px);
        }

        @include mediaQuery($tablet-lg) {
            font-size: $font_heading_lg;
        }

        animation: text-fade 4000ms 2000ms;

        &:before,
        &:after {
            content: '';
            opacity: 0.7;
            position: absolute;
            width: 15%;
            height: 50%;
            background-color: transparent;
            transition: all 1000ms ease-in-out;
            animation: border 7000ms 1000ms;
        }

        &:before {
            top: -15px;
            left: $horizontal_sm;
            border-top: $border_sm;
            border-left: $border_sm;

            @include mediaQuery($mobile-sm) {
                border-top: calc(0.003 * 100vw + 3.04px) solid $color-white;
                border-left: calc(0.003 * 100vw + 3.04px) solid $color-white;
                left: calc(-0.014 * 100vw - 15.52px);
            }

            @include mediaQuery($tablet-lg) {
                border-top: $border_lg;
                border-left: $border_lg;
                left: $horizontal_lg;
            }
        }

        &:after {
            bottom: -20px;
            right: $horizontal_sm;
            border-bottom: $border_sm;
            border-right: $border_sm;

            @include mediaQuery($mobile-sm) {
                border-bottom: calc(0.003 * 100vw + 3.04px) solid $color-white;
                border-right: calc(0.003 * 100vw + 3.04px) solid $color-white;
                right: calc(-0.014 * 100vw - 15.52px);
            }

            @include mediaQuery($tablet-lg) {
                border-bottom: $border_lg;
                border-right: $border_lg;
                right: $horizontal_lg;
            }
        }
    }

    .heading_arrow {
        width: $arrow_sm;
        height: $arrow_sm;
        position: absolute;
        bottom: 10%;
        transform: scale(1);
        cursor: pointer;
        animation: pulse 1s infinite;

        @include mediaQuery($mobile-sm) {
            width: calc(0.028 * 100vw + 50.91px);
            height: calc(0.028 * 100vw + 50.91px);
        }

        @include mediaQuery($tablet-lg) {
            width: $arrow_lg;
            height: $arrow_lg;
        }

        svg {
            circle,
            polyline,
            line {
                color: white;
            }
        }
    }
}
