@import '../../../media.scss';
$icon_sm: 55px;
$icon_lg: 100px;
$border_sm: 3px solid $color-white;

.booking_container {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding-top: 16px;

    @include mediaQuery($tablet-sm) {
        @include background_shadow;
        margin-top: 40vh;
        padding: 32px 0;
    }

    p:last-child {
        margin: 0;
    }

    .booking_heading {
        @include heading_decorator;
    }

    .booking {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        @include mediaQuery($tablet-lg) {
            p {
                &:last-child {
                    display: none;
                }
            }
        }

        .booking_content {
            width: 100%;

            @include mediaQuery($tablet-sm) {
                width: 90%;
                padding-right: 32px;
            }

            .booking_paragraph {
                text-align: left;
            }

            .booking_forms {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;

                .booking_forms_phone {
                    width: 90%;
                    display: flex;
                    justify-content: space-around;
                    align-items: center;
                    margin-bottom: 1rem;
                    font-size: $font_md;

                    @include mediaQuery($mobile-lg) {
                        width: 60%;
                    }

                    @include mediaQuery($tablet-sm) {
                        padding: 1vw 0;
                    }

                    @include mediaQuery($tablet-lg) {
                        padding: 0;
                    }

                    a {
                        color: $color-white;
                        text-align: center;
                        width: 70%;
                        font-size: $font_md;

                        @include mediaQuery($mobile-md) {
                            width: 60%;
                        }

                        @include mediaQuery($tablet-lg) {
                            width: 100%;
                            font-size: $font_md-custom;
                        }
                    }

                    .booking_forms_img {
                        width: $icon_sm;
                        height: $icon_sm;

                        @include mediaQuery($mobile-sm) {
                            width: calc(0.064 * 100vw + 34.52px);
                            height: calc(0.064 * 100vw + 34.52px);
                        }

                        @include mediaQuery($tablet-lg) {
                            width: $icon_lg;
                            height: $icon_lg;
                            display: none;
                        }
                    }
                }

                .btn {
                    font-size: $font_md;
                    width: 60%;

                    @include mediaQuery($tablet-lg) {
                        width: 100%;
                    }
                }
            }
        }
    }

    .booking_gallery {
        width: 80%;
        height: 100%;
        display: none;
        margin-left: 0;

        @include mediaQuery($tablet-lg) {
            display: block;
        }
    }
}
