@import '../../../media.scss';

.about_container {
    max-width: 100%;
    padding: 0;

    @include mediaQuery($tablet-sm) {
        @include background_shadow;
    }

    .about {
        width: 100%;
        height: 100%;
        padding-top: 16px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        @include mediaQuery($tablet-sm) {
            padding-bottom: 16px;
        }

        @include mediaQuery($tablet-lg) {
            padding-bottom: 32px;
        }

        .about_heading {
            @include heading_decorator;
        }

        .about_paragraph {
            text-align: left;
        }
    }
}
