@import '../../../media.scss';

$icon_sm: 50px;
$icon_lg: 100px;

@mixin responsive-icons {
    width: $icon_sm;
    height: $icon_sm;
    @include mediaQuery($mobile-sm) {
        width: calc(0.071 * 100vw + 27.28px);
        height: calc(0.071 * 100vw + 27.28px);
    }

    @include mediaQuery($tablet-lg) {
        width: $icon_lg;
        height: $icon_lg;
    }
}

.footericon_container {
    display: inline-block;
    cursor: pointer;
    overflow: hidden;
    margin: 0 5px;
    transition: width 0.4s ease;
    color: $color-white;
    text-decoration: none;
    @include responsive-icons;
    @include mediaQuery($tablet-lg) {
        &:hover {
            width: 400px;
        }

        &:first-child,
        &:last-child {
            &:hover {
                width: 300px;
            }
        }
    }

    &:hover {
        color: $color-white;
        text-decoration: none;
    }

    .footericon {
        display: inline-block;
        text-align: center;
        width: $icon_lg;
        height: $icon_lg;
        @include responsive-icons;

        .footericon_img {
            width: $icon_sm;
            height: $icon_sm;
            @include responsive-icons;
        }
    }

    .footer_text {
        font-size: 22px;
        line-height: 60px;
        text-align: center;
        width: 300px;
        margin-left: 25px;
        display: none;

        @include mediaQuery($tablet-lg) {
            display: inline;
        }
    }
}
