@import '../../../media.scss';

.price_container {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 16px;

    @include mediaQuery($tablet-sm) {
        @include background_shadow;
        margin-top: 40vh;
        padding: 32px 0;
    }

    .price_heading {
        @include heading_decorator;
    }

    .price {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0;

        @include mediaQuery($tablet-lg) {
            flex-direction: row-reverse;
        }

        .price_content {
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;

            @include mediaQuery($tablet-sm) {
                width: 90%;
            }

            @include mediaQuery($tablet-lg) {
                padding-left: 48px;
            }

            .price_img {
                max-width: 100%;
            }
        }

        .price_gallery {
            width: 80%;
            height: 100%;
            display: none;
            margin-right: 0;

            @include mediaQuery($tablet-lg) {
                display: block;
            }
        }
    }
}
