@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;1,400;1,500&display=swap');
@import './media.scss';
html,
body {
    // width: 100%;
    // height: 100%;
    box-sizing: border-box;
}

body {
    color: $text-color-default;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: 'Roboto', sans-serif;
    overflow-x: hidden;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

p,
td {
    font-size: $font_sm;
    line-height: $initialLineHeight;

    @include mediaQuery($mobile-sm) {
        font-size: calc(0.003 * 100vw + 15.01px);
        line-height: calc(0.009 * 100vw + 23.12px);
    }

    @include mediaQuery($tablet-lg) {
        font-size: $font_md;
        line-height: $finalFineHeight;
    }
}

p {
    @include mediaQuery($tablet-lg) {
        text-align: justify !important;
    }
}

h2 {
    font-size: $font_lg;
    text-align: center;
    margin: 8vw auto 10vw auto;
    transition: margin 0.2s ease;
    display: inline-block;

    @include mediaQuery($mobile-sm) {
        font-size: calc(0.023 * 100vw + 24.7px);
    }

    @include mediaQuery($tablet-sm) {
        margin: 4vw auto 5vw auto;
    }

    @include mediaQuery($tablet-lg) {
        margin: 1vw auto 2vw auto;
        font-size: $font_xl;
    }
}

h3 {
    font-size: $font_md-custom;
    margin-bottom: $font_xs;

    @include mediaQuery($mobile-sm) {
        font-size: calc(0.006 * 100vw + 26.08px);
        margin-bottom: calc(0.011 * 100vw + 4.48px);
    }

    @include mediaQuery($tablet-lg) {
        font-size: $font_lg;
        margin-bottom: $font_sm;
    }
}

a {
    font-size: $font_sm;

    @include mediaQuery($mobile-sm) {
        font-size: calc(0.009 * 100vw + 15.27px);
    }

    @include mediaQuery($tablet-lg) {
        font-size: $font_md-custom-s;
    }
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px #dbdbdb;
}

::-webkit-scrollbar-thumb {
    background: #c7c7c7;
}

::-webkit-scrollbar-thumb:hover {
    background: #999999;
}
