@import '../../../media.scss';

.sidebar {
    z-index: 99;
    transform: translateX(-120%);
    transition: transform 500ms ease-in-out;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $color-black;
    -webkit-box-shadow: 4px 0px 8px 4px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 4px 0px 8px 4px rgba(0, 0, 0, 0.75);
    box-shadow: 4px 0px 8px 4px rgba(0, 0, 0, 0.75);

    &.sidebar-active {
        transform: translateX(0);
    }

    .sidebar_nav-item {
        height: 10vh;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 2vh 10vw;
        border-bottom: 4px solid #ffffff33;
        color: $text-color-default;
        font-size: $font-md;
        font-weight: 500;
        text-decoration: none;

        @include mediaQuery($mobile-sm) {
            font-size: calc(0.011 * 100vw + 20.48px);
        }

        @include mediaQuery($tablet-lg) {
            font-size: $font-lg;
        }

        &:hover {
            color: $highlight-color-blue !important;

            border-bottom: 4px solid $highlight-color-blue;
        }
    }
}
