@mixin for-s-tablet-down {
    @media (max-width: 720px) {
        @content;
    }
}
@mixin for-mobile-portrait {
    @media (max-width: 600px) {
        @content;
    }
}

.icons_grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 275px);
    grid-gap: 1rem;
    justify-items: center;
    width: 100%;
    margin: 0 auto;
    padding: 1rem 0 0 0;

    @include for-s-tablet-down {
        display: flex;
        flex-direction: column;
    }
    @include for-mobile-portrait {
        padding: 1rem 0 0 0;
    }
}
