@import '../../../../../media.scss';

.iconcard {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;

    &.iconcard_item-1 {
        grid-column-start: 1;
        grid-column-end: 1;
        grid-row-start: 1;
        grid-row-end: 1;
    }

    &.iconcard_item-3 {
        grid-column-start: 2;
        grid-column-end: 2;
        grid-row-start: 2;
        grid-row-end: 2;
    }

    .iconcard_image {
        width: 100%;
        height: 100%;
        object-fit: cover;
        display: flex;
        justify-content: center;
        margin: 0;

        img {
            width: 45%;
            height: 50%;
            transition: all 0.2s ease;

            @include mediaQuery($mobile-lg) {
                width: 40%;
            }

            @media (min-width: 720px) {
                width: 90%;
                height: 80%;
            }

            @include mediaQuery($tablet-sm) {
                width: 70%;
                height: 85%;
            }

            @media (min-width: 992px) {
                width: 60%;
                height: 90%;
            }

            @include mediaQuery($screen-sm) {
                width: 55%;
                height: 100%;
            }
        }
    }
}
