@mixin for-desktop-down {
    @media (max-width: 1500px) {
        @content;
    }
}

@mixin for-tablet-down {
    @media (max-width: 957px) {
        @content;
    }
}

@mixin for-s-tablet-down {
    @media (max-width: 720px) {
        @content;
    }
}
.gallerycard {
    width: 100%;
    height: 100%;
    display: flex;

    &.gallerycard__item-1 {
        grid-column-start: 1;
        grid-column-end: 1;
        grid-row-start: 1;
        grid-row-end: 3;
        @include for-tablet-down {
            grid-area: 1/1/3/3;
        }
    }

    &.gallerycard__item-2 {
        grid-column-start: 2;
        grid-column-end: 4;
        grid-row-start: 1;
        grid-row-end: 2;
        @include for-tablet-down {
            display: none;
        }
    }
    &.gallerycard__item-3 {
        grid-column-start: 2;
        grid-column-end: 4;
        grid-row-start: 2;
        grid-row-end: 3;
        @include for-tablet-down {
            display: none;
        }
    }
    .gallerycard__image {
        margin: 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}
